:root {
  --accent-color: orange;
  --text-color: #000;
  --silver-color: silver;
}
* {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}
body {
  margin: 0;
  font-family: "Montserrat", sans-serif;
  /* -webkit-font-feature-settings: anti; */
  color: #000;
  color: var(--text-color);
  background-color: silver;
  background-color: var(--silver-color);
}

